@media only screen and (max-width: 980px) {
	#header .header_top, 
	#header .footer_top,
	#footer .header_top, 
	#footer .footer_top {
		padding: 0 30px;
	}
	#header .header_bottom, 
	#header .footer_bottom, 
	#footer .header_bottom, 
	#footer .footer_bottom {
		padding: 28px 30px 23px;
	}
	#main {
		.wrapper_slider {
			.bg {
				padding: 0 15px;
			}
		}
	}
	#header .header_top .wrapper_nav .nav, 
	#header .footer_top .wrapper_nav .nav, 
	#footer .header_top .wrapper_nav .nav, 
	#footer .footer_top .wrapper_nav .nav {
		display: none;
	}
	#header .header_top .wrapper_nav .mob-menu, 
	#header .footer_top .wrapper_nav .mob-menu, 
	#footer .header_top .wrapper_nav .mob-menu,
	#footer .footer_top .wrapper_nav .mob-menu {
		display: block;
	}
} 	